<template>
    <div>
        <el-form ref="form" label-width="80px">
            <el-form-item label="姓氏">
                <el-input v-model="firstName"></el-input>
            </el-form-item>
            <el-form-item label="位置">
                <el-radio v-model="matchIndex" label="1">一字</el-radio>
                <el-radio v-model="matchIndex" label="2">二字</el-radio>
            </el-form-item>
            <el-form-item label="定字">
                <el-input v-model="matchName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">匹配</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                firstName  : "陈",
                matchName  : "越",
                matchIndex : "1"
            }
        },
        
        methods: {
            onSubmit() {
                this.$router.push({ name: 'Show', params: { 
                    firstName  : this.firstName,
                    matchIndex : this.matchIndex,
                    matchName  : this.matchName
                }})
            }
        }
    }
</script>


